  </div>
</template>

<script>
const axios = require("axios");
import Qs from 'qs';
export default {
  name: "dashboard",
  data() {
    return {
      activeName: "denglu",
      formInline: {
          value1: '',
          value2: '',
          value3: '',
          value4: '',
          value5: ''
        },
      tableData:[],
      tableData1:[],
      tableData2:[],
      Page:1,
      PageSize:10,
      QueryWhere:'',
      UserName:'',
      SessionKey:'',
      TotalCount:'',
      StartTime:'',
      EndTime:''
    };
  },
  methods: {
    handleClick(tab, event) {
        console.log(tab, event);
    },
    tableRowClassName({row, rowIndex}) {
        if (rowIndex === 0||rowIndex === 1||rowIndex === 2||rowIndex === 3||rowIndex === 4||rowIndex === 5||rowIndex === 6) {
            return 'warning-row';
        }
        return '';
    },
    tableRowClassName2({row, rowIndex}) {
        if (rowIndex === 0||rowIndex === 1||rowIndex === 2) {
            return 'warning-row';
        }
        return '';
    },
    getData(){
          axios({
            method: "post",
            url: http+"api/ExamineWeb/GetLogList",
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: Qs.stringify({
                Page: this.Page,
                PageSize: this.PageSize,
                QueryWhere:this.QueryWhere,
                UserName:this.UserName,
                SessionKey:this.SessionKey,
                Type:1,
                StartTime:'',
                EndTime:''
            })
        })
        .then(res => {
            console.log(res);
            if (res.data.IsSuccess) {
                this.TotalCount = res.data.TotalCount
                this.tableData = res.data.Data
            }
        })
        .catch(error => {
            this.$message({
                showClose: true,
                message: "服务器错误",
                type: "error"
            });
        });
      },
      onSubmit(){
          console.log('查询')
      }
    },
    mounted(){
        this.UserName = sessionStorage.getItem("UserName")
        this.SessionKey = sessionStorage.getItem("SessionKey")
        this.getData()
    }
};
</script>

<style scoped>
.num-panel .box-card .header i {
  font-size: 24px;
}
